:root {
  --header:     #F72533;
  --back:       #121A24;
  --other:      #223144;
  --highlight:  #053269;
  --text:       #ffffff;
}

h1 {
    color: var(--header);
    display: flex;
    justify-content: center;
}

.fancy{
    display: flex;
    justify-content: center;
    margin: 25px 0;
    border-collapse: collapse;
}

.fancy thead tr {
    background-color: ;
    color: var(--text);
    text-align: left;
}

.fancy th,
.fancy td {
    padding: 12px 15px;
    min-width: 20vw;
}


.fancy tbody tr:nth-of-type(even) {
    background-color: var(--other);
}

.fancy tr:first-of-type {
    background-color: var(--highlight);
    font-size: 20px;
    font-weight: bold;
}


form {
  display: flex;
  justify-content: center;
}

.decrement button{
  display: flex;
  justify-content: center;
  margin-left: 300px;
}

.increment button{
  margin-left: 300px;
  display: flex;
}

.page-select {
  font-size: 20px;
  font-weight: bold;
  border: none;
  margin: 5px;
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  background-color: var(--highlight);
  color: var(--text);
}

.page-select:hover {
  background-color: var(--text);
  color: var(--highlight);
}

.round {
  border-radius: 50%;
}
